.info {
  max-width: 360px;
  margin: 0 auto;
  padding: 20px 0 20px;

  .icon {
    color: $gray-color;

    > i {
      font-size: 3.85rem;
    }
  }
  .info-title {
    color: $black-color;
    margin: 0.875rem * 2 0 0.875rem;
  }
  p {
    color: $gray-color;
  }
}

.info-horizontal {
  .icon {
    float: left;
    margin-top: 24px;
    margin-right: 10px;

    > i {
      font-size: $h2-font-size;
    }
  }
  .description {
    overflow: hidden;
  }
}

.icon {
  &.icon-primary {
    color: $primary;
  }
  &.icon-info {
    color: $info;
  }
  &.icon-success {
    color: $success;
  }
  &.icon-warning {
    color: $warning;
  }
  &.icon-danger {
    color: $danger;
  }
  &.icon-rose {
    color: $rose;
  }
}
